*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: #292524;
    color: #fafaf9;
    font-family: "Sono" , sans-serif;
    padding: 48px 60px 0px;
}

.hidden {
    display: none !important;
}

.message{
    text-align: center;
    text-transform: uppercase;
    font-size: 32px;
    font-weight: 600;
    margin-top: 24px;
}

.disputed {
    color: #ef4444;
    font-weight: 600;
    margin-right: 10px;
}

/* MAIN LAYOUT*/
#root {
    max-width: 1500px;
    margin: 0 auto;

}

.header{
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.main {
    display: grid;
    grid-template-columns: 250px 1fr;
    gap: 48px;

    height: calc(100vh - 48px - 68px - 40px);
}

@media (max-width: 900px) {
    .main{
        grid-template-columns: 1fr;

        height: auto;
    }
}

section {
    padding-bottom: 40px;
   
    overflow-y: scroll;
    -ms-overflow-style: none; /* for Internet Explorer */
    scrollbar-width: none; /* disable scroll bars for Firefox */
  }
   
  section::-webkit-scrollbar {
    display: none; /* disable scroll bars for Chrome, Safari, Edge and Opera */
  }
/* ELEMENT */

.logo {
    display: flex;
    align-items: center;
    gap: 16px;

}

.logo img {
    width: 68px;
    height: 68px;
}

h1 {
    font-size: 42px;
    text-transform: uppercase;
    font-family: "Coiny" , sans-serif;
    color: #fafaf9;
    line-height: 1;
    margin-top: 6px;
}

.category{
    margin-bottom: 16px;

}

.fact {
    font-size: 20px;
    line-height: 1.4;
    color: #fafaf9;
    background-color: #44403c;
    margin-bottom: 16px;
    padding: 16px 24px;
    letter-spacing: -1px;
    border-radius: 16px;
    display: flex;
    align-items: center;
    gap: 24px;
}

@media (max-width: 1200px){
    .fact{
        flex-direction: column;
        align-items: flex-end;
        gap: 12px;
    }

}

.vote-buttons{
    margin-left: auto;
    flex-shrink: 0;

    display: flex;
    gap: 8px;
}

.vote-buttons button {
    border: none;
    background-color: #78716c;
    font-size: 18px;
    padding: 6px 12px;
    border-radius: 100px;
    color: inherit;
    font-family: inherit;
    font-weight: 600;
    cursor: pointer;
    transition: 0.3s;
}

.vote-buttons button:hover {
    background-color: #292524;
}

.vote-buttons button:disabled {
    background-color: #44403c;
}

.tag {
    text-transform: uppercase;
    font-size: 14px;
    font-family: "Coiny" , sans-serif;
    color: #fafaf9;
    padding-top: 3px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 100px;
}

ul {
    list-style: none;
}

.source:link, .source:visited {
    text-decoration: none;
    color: #a8a29e;
    margin-left: 12px;
    transition: 0.3s ;
}

.source:hover, .source:active {
    color: #3b82f6;
}

.btn {
    border: none;
    font-family: "Coiny", sans-serif;
    line-height: 1;
    text-transform: uppercase;
    font-size: 17px;
    padding: 16px 0 13px;
    background-image: linear-gradient(135deg, #3b82f6, #ef4444, #16a34a, #eab308);
    color: inherit;
    transition: 0.3s;
    border-radius: 100px;
    cursor: pointer;
}

.btn:hover {
    transform: scale(110%) rotate(-2deg);
}

.btn-large {
    font-size: 20px;
    padding: 20px 32px 17px;
}

.btn-all-categories {
    margin-bottom: 16px;
    width: 100%;
}

.btn-catergory {
    width: 100%;
    background-image: none;

}

/* FORM */
.fact-form{
    margin-bottom: 40px;
    background-color: #44403c;
    padding: 16px 32px;
    
    display: flex;
    align-items: center;
    gap: 16px;
    border-radius: 16px;

}

.fact-form input, 
.fact-form select {
    width: 220px;
    background-color: #78716c;
    border: none;
    border-radius: 100px;
    padding: 16px;
    font-size: 18px;
    color: inherit;
    font-family: inherit;
}

.fact-form input::placeholder {
    color: #a8a29e;

}

.fact-form input:first-child {
    flex-grow: 1;
}

.fact-form span {
    font-weight: 600;
    font-size: 18px;
    margin-right: 18px ;
}

@media (max-width: 1000px) {
    body {
       /* padding: 32px 48px;*/

    }

    .fact-form {
        flex-direction: column;
        align-items: stretch;

    }

    .fact-form input,
    .fact-form select {
        width: auto;
    }
}